
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
}
#app .app-wrapper {
    height: 100%;
}
#app .app-wrapper .app-slider-tab {
      background-color: #545c64;
}
#app .app-wrapper .app-slider-tab .app-logo {
        color: #fff;
        height: 50px;
        line-height: 50px;
        border-bottom: 1px solid #555;
}
#app .app-wrapper .app-slider-tab .el-menu {
        border-right: none;
}
#app .app-wrapper .app-slider-tab .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
}
#app .app-wrapper .app-main-container .app-header {
      height: 50px;
      background-color: #545455;
      border-bottom: 1px solid #e5e5e5;
}
#app .app-wrapper .app-main-container .app-header .header-collapse {
        background-color: #444;
        padding: 8px;
}
#app .app-wrapper .app-main-container .app-header .el-dropdown-link {
        padding: 10px;
}
#app .app-wrapper .app-main-container .app-header .el-dropdown-link:hover {
          cursor: pointer;
}
#app .app-wrapper .app-main-container .app-header .el-dropdown-link svg {
          font-size: 30px;
}
#app .app-wrapper .app-main-container .app-header svg {
        font-size: 24px;
        color: #ccc;
}
#app .app-wrapper .app-main-container .app-content {
      overflow: auto;
      -webkit-transition: all 0.05s ease-in-out;
      transition: all 0.05s ease-in-out;
}
#app .app-wrapper .app-main-container .app-footer {
      height: 40px;
      line-height: 40px;
      font-size: 13px;
      border-top: 1px solid #e5e5e5;
}
.fade-enter, .fade-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.first {
  background-color: yellow;
}

.second {
  background-color: red;
}

.third {
  background-color: green;
}

.fouth {
  background-color: blue;
}
